import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  Box,
  Divider,
  Alert,
  Checkbox,
  FormControlLabel,
  IconButton,
  Paper
} from '@mui/material';
import {
  Close as CloseIcon,
  Gavel as GavelIcon,
  Security as SecurityIcon
} from '@mui/icons-material';

export default function ContratoModal({ open, onClose, onAceitar, dadosInstituicao }) {
  const [aceitou, setAceitou] = useState(false);
  const dataAtual = new Date().toLocaleDateString('pt-BR');
  const horaAtual = new Date().toLocaleTimeString('pt-BR');

  const handleAceitar = () => {
    if (!aceitou) {
      alert('Voc precisa marcar a opo "Li e aceito" para continuar.');
      return;
    }

    // Salvar aceite com timestamp
    const aceiteContrato = {
      aceitou: true,
      data: new Date().toISOString(),
      dataFormatada: `${dataAtual} s ${horaAtual}`,
      nomeInstituicao: dadosInstituicao.nome,
      cnpj: dadosInstituicao.cnpj,
      responsavel: dadosInstituicao.responsavel,
      plano: dadosInstituicao.plano
    };

    onAceitar(aceiteContrato);
  };

  return (
    <Dialog
      open={open}
      onClose={() => {}} // Desabilitar fechar clicando fora
      maxWidth="md"
      fullWidth
      disableEscapeKeyDown // Desabilitar ESC
    >
      <DialogTitle sx={{ bgcolor: 'primary.main', color: 'white', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <GavelIcon />
          <Typography variant="h6">Contrato de Licena de Uso de Software</Typography>
        </Box>
        <IconButton onClick={onClose} sx={{ color: 'white' }}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>

      <DialogContent sx={{ mt: 2 }}>
        {/* Aviso Importante */}
        <Alert severity="warning" icon={<SecurityIcon />} sx={{ mb: 2 }}>
          <Typography variant="subtitle2" fontWeight="bold">
            LEIA ATENTAMENTE ANTES DE ACEITAR
          </Typography>
          <Typography variant="body2">
            Este  um contrato legalmente vinculante. Ao aceitar, voc concorda com todos os termos.
          </Typography>
        </Alert>

        {/* Identificao */}
        <Paper elevation={0} sx={{ bgcolor: '#f5f5f5', p: 2, mb: 2 }}>
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            PARTES CONTRATANTES
          </Typography>
          <Divider sx={{ my: 1 }} />
          <Typography variant="body2" paragraph>
            <strong>CONTRATANTE (Licenciado):</strong><br />
            Instituio: {dadosInstituicao.nome || '[NOME DA INSTITUIO]'}<br />
            CNPJ: {dadosInstituicao.cnpj || '[CNPJ]'}<br />
            Responsvel: {dadosInstituicao.responsavel || '[RESPONSVEL]'}<br />
            Plano: {dadosInstituicao.plano || '[PLANO]'}
          </Typography>
          <Typography variant="body2">
            <strong>CONTRATADO (Licenciante):</strong><br />
            Nome: Wander Pires Silva Coelho<br />
            Software: CEI - Controle Escolar Inteligente<br />
            Modalidade: SaaS (Software as a Service)
          </Typography>
        </Paper>

        {/* Contrato */}
        <Box sx={{ maxHeight: 400, overflowY: 'auto', border: '1px solid #ddd', p: 2, borderRadius: 1 }}>
          <Typography variant="h6" align="center" gutterBottom sx={{ fontWeight: 'bold' }}>
            CONTRATO DE LICENA DE USO DE SOFTWARE
          </Typography>
          
          <Typography variant="caption" align="center" display="block" gutterBottom>
            Data: {dataAtual} | Hora: {horaAtual}
          </Typography>

          <Divider sx={{ my: 2 }} />

          {/* Clusula 1 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 1 - DO OBJETO
          </Typography>
          <Typography variant="body2" paragraph>
            1.1. O presente contrato tem por objeto a concesso de licena de uso do software denominado 
            <strong> CEI - Controle Escolar Inteligente</strong>, de propriedade exclusiva do CONTRATADO.
          </Typography>
          <Typography variant="body2" paragraph>
            1.2. O software ser fornecido na modalidade SaaS (Software as a Service), acessado via navegador 
            web, sem instalao local.
          </Typography>

          {/* Clusula 2 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 2 - DA LICENA
          </Typography>
          <Typography variant="body2" paragraph>
            2.1. A licena  concedida de forma <strong>NO EXCLUSIVA</strong>, <strong>NO TRANSFERVEL</strong> e 
            <strong> ONEROSA</strong>, pelo prazo determinado no plano contratado.
          </Typography>
          <Typography variant="body2" paragraph>
            2.2. A licena permite o uso do software exclusivamente para os fins da instituio contratante, 
            sendo vedada a sublicena, cesso ou transferncia a terceiros.
          </Typography>

          {/* Clusula 3 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 3 - DO VALOR E PAGAMENTO
          </Typography>
          <Typography variant="body2" paragraph>
            3.1. O CONTRATANTE pagar ao CONTRATADO o valor mensal conforme plano escolhido:
          </Typography>
          <Typography variant="body2" component="ul" sx={{ pl: 3, mb: 2 }}>
            <li><strong>Plano Bsico</strong>: R$ 29,90/ms</li>
            <li><strong>Plano Intermedirio</strong>: R$ 49,90/ms</li>
            <li><strong>Plano Premium</strong>: R$ 79,90/ms</li>
          </Typography>
          <Typography variant="body2" paragraph>
            3.2. O pagamento ser realizado mensalmente atravs do Mercado Pago (PIX, Carto ou Boleto).
          </Typography>
          <Typography variant="body2" paragraph>
            3.3. O no pagamento no prazo de 30 dias implica em suspenso automtica do acesso ao sistema.
          </Typography>

          {/* Clusula 4 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 4 - DA PROPRIEDADE INTELECTUAL
          </Typography>
          <Typography variant="body2" paragraph>
            4.1. O software  <strong>PROPRIEDADE EXCLUSIVA</strong> do CONTRATADO, protegido pelas 
            Leis n 9.609/98 (Lei do Software) e n 9.610/98 (Direitos Autorais).
          </Typography>
          <Typography variant="body2" paragraph>
            4.2.  <strong>EXPRESSAMENTE PROIBIDO</strong>: copiar, modificar, fazer engenharia reversa, 
            descompilar, sublicenciar ou distribuir o software.
          </Typography>
          <Typography variant="body2" paragraph color="error">
            4.3. Violaes acarretaro <strong>processo criminal</strong> (Art. 184 do CP - deteno de 6 meses a 2 anos) 
            e <strong>indenizao mnima de R$ 50.000,00</strong>.
          </Typography>

          {/* Clusula 5 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 5 - DAS OBRIGAES DO CONTRATANTE
          </Typography>
          <Typography variant="body2" component="ul" sx={{ pl: 3, mb: 2 }}>
            <li>Utilizar o software exclusivamente para fins legais</li>
            <li>Manter sigilo das credenciais de acesso</li>
            <li>No compartilhar acesso com terceiros no autorizados</li>
            <li>Efetuar pagamentos nos prazos estabelecidos</li>
            <li>Respeitar os direitos de propriedade intelectual</li>
          </Typography>

          {/* Clusula 6 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 6 - DAS OBRIGAES DO CONTRATADO
          </Typography>
          <Typography variant="body2" component="ul" sx={{ pl: 3, mb: 2 }}>
            <li>Manter o sistema disponvel 99% do tempo</li>
            <li>Fornecer suporte tcnico conforme plano contratado</li>
            <li>Proteger dados conforme LGPD (Lei 13.709/18)</li>
            <li>Realizar backups dirios dos dados</li>
            <li>Notificar manutenes com antecedncia</li>
          </Typography>

          {/* Clusula 7 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 7 - DA PROTEO DE DADOS (LGPD)
          </Typography>
          <Typography variant="body2" paragraph>
            7.1. O CONTRATADO compromete-se a tratar os dados pessoais em conformidade com a Lei 13.709/18 (LGPD).
          </Typography>
          <Typography variant="body2" paragraph>
            7.2. Os dados sero utilizados exclusivamente para prestao do servio contratado.
          </Typography>
          <Typography variant="body2" paragraph>
            7.3. O CONTRATANTE tem direito de acesso, correo, excluso e portabilidade de seus dados.
          </Typography>

          {/* Clusula 8 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 8 - DA VIGNCIA E RESCISO
          </Typography>
          <Typography variant="body2" paragraph>
            8.1. O contrato vigora por prazo indeterminado, enquanto houver pagamento regular.
          </Typography>
          <Typography variant="body2" paragraph>
            8.2. Qualquer parte pode rescindir mediante comunicao prvia de 30 dias.
          </Typography>
          <Typography variant="body2" paragraph>
            8.3. O CONTRATADO pode rescindir imediatamente em caso de violao dos termos.
          </Typography>
          <Typography variant="body2" paragraph>
            8.4. Aps resciso, os dados sero mantidos por 30 dias e ento excludos permanentemente.
          </Typography>

          {/* Clusula 9 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 9 - DA GARANTIA E RESPONSABILIDADE
          </Typography>
          <Typography variant="body2" paragraph>
            9.1. O software  fornecido "AS IS" (como est), com garantia de 7 dias para reembolso.
          </Typography>
          <Typography variant="body2" paragraph>
            9.2. O CONTRATADO no se responsabiliza por: perda de dados causada pelo usurio, 
            interrupes de internet, erros de operao ou decises baseadas nos relatrios.
          </Typography>
          <Typography variant="body2" paragraph>
            9.3. A responsabilidade do CONTRATADO est limitada ao valor pago nos ltimos 12 meses.
          </Typography>

          {/* Clusula 10 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 10 - DA CONFIDENCIALIDADE
          </Typography>
          <Typography variant="body2" paragraph>
            10.1. As partes comprometem-se a manter sigilo sobre informaes confidenciais.
          </Typography>
          <Typography variant="body2" paragraph>
            10.2. O CONTRATANTE no divulgar detalhes tcnicos ou funcionamento interno do software.
          </Typography>

          {/* Clusula 11 */}
          <Typography variant="subtitle2" fontWeight="bold" gutterBottom>
            CLUSULA 11 - DO FORO
          </Typography>
          <Typography variant="body2" paragraph>
            11.1. Fica eleito o foro da comarca do CONTRATADO para dirimir quaisquer dvidas oriundas 
            deste contrato, com excluso de qualquer outro, por mais privilegiado que seja.
          </Typography>

          {/* Assinatura Digital */}
          <Divider sx={{ my: 2 }} />
          <Typography variant="caption" color="text.secondary" align="center" display="block">
            Este contrato foi gerado eletronicamente e tem validade jurdica conforme<br />
            Lei 9.609/98, Lei 9.610/98 e Marco Civil da Internet (Lei 12.965/14)
          </Typography>
          <Divider sx={{ my: 2 }} />

          <Typography variant="body2" align="center" fontWeight="bold">
             2026 Wander Pires Silva Coelho - Todos os direitos reservados
          </Typography>
        </Box>

        {/* Checkbox de aceite */}
        <Box sx={{ mt: 2 }}>
          <FormControlLabel
            control={
              <Checkbox
                checked={aceitou}
                onChange={(e) => setAceitou(e.target.checked)}
                color="primary"
              />
            }
            label={
              <Typography variant="body2">
                <strong>Declaro que li, compreendi e aceito integralmente todos os termos deste contrato</strong>, 
                reconhecendo que ele tem validade jurdica e que estou ciente das obrigaes e penalidades previstas.
              </Typography>
            }
          />
        </Box>

        {/* Informao do aceite */}
        {aceitou && (
          <Alert severity="success" sx={{ mt: 2 }}>
            <Typography variant="body2">
              <strong>Aceite registrado em:</strong> {dataAtual} s {horaAtual}
            </Typography>
          </Alert>
        )}
      </DialogContent>

      <DialogActions sx={{ p: 2, bgcolor: '#f5f5f5' }}>
        <Button onClick={onClose} variant="outlined">
          Cancelar
        </Button>
        <Button
          onClick={handleAceitar}
          variant="contained"
          disabled={!aceitou}
          startIcon={<GavelIcon />}
        >
          Aceitar Contrato e Continuar
        </Button>
      </DialogActions>
    </Dialog>
  );
}
