import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useData } from '../context/DataContext';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  Alert,
  Stepper,
  Step,
  StepLabel,
  Grid,
  MenuItem,
  FormHelperText
} from '@mui/material';
import SchoolIcon from '@mui/icons-material/School';
import ContratoModal from '../components/ContratoModal';

export default function CadastroEscolaPage() {
  const { getPlanosAtivos } = useData();
  const navigate = useNavigate();
  const [etapa, setEtapa] = useState(0);
  const [erro, setErro] = useState('');
  const [mostrarContrato, setMostrarContrato] = useState(false);
  const [contratoAceito, setContratoAceito] = useState(null);
  const [formData, setFormData] = useState({
    // Dados da Instituio
    nomeInstituicao: '',
    cnpj: '',
    inep: '',
    telefone: '',
    email: '',
    endereco: '',
    cidade: '',
    estado: '',
    cep: '',
    planoId: '',
    
    // Dados do Responsvel
    nomeResponsavel: '',
    cpfResponsavel: '',
    telefoneResponsavel: '',
    emailResponsavel: '',
    cargoResponsavel: '',
    
    // Dados de Acesso
    loginAdmin: '',
    senhaAdmin: '',
    confirmarSenha: ''
  });

  const planosDisponiveis = getPlanosAtivos();
  const etapas = ['Dados da Instituio', 'Responsvel', 'Criar Acesso'];

  // Funes de mscara
  const aplicarMascaraCPF = (valor) => {
    valor = valor.replace(/\D/g, '');
    if (valor.length <= 11) {
      valor = valor.replace(/(\d{3})(\d)/, '$1.$2');
      valor = valor.replace(/(\d{3})(\d)/, '$1.$2');
      valor = valor.replace(/(\d{3})(\d{1,2})$/, '$1-$2');
    }
    return valor;
  };

  const aplicarMascaraCNPJ = (valor) => {
    valor = valor.replace(/\D/g, '');
    if (valor.length <= 14) {
      valor = valor.replace(/^(\d{2})(\d)/, '$1.$2');
      valor = valor.replace(/^(\d{2})\.(\d{3})(\d)/, '$1.$2.$3');
      valor = valor.replace(/\.(\d{3})(\d)/, '.$1/$2');
      valor = valor.replace(/(\d{4})(\d)/, '$1-$2');
    }
    return valor;
  };

  const aplicarMascaraCelular = (valor) => {
    valor = valor.replace(/\D/g, '');
    if (valor.length <= 11) {
      valor = valor.replace(/^(\d{2})(\d)/, '($1) $2');
      valor = valor.replace(/(\d{5})(\d)/, '$1-$2');
    }
    return valor;
  };

  const aplicarMascaraTelefone = (valor) => {
    valor = valor.replace(/\D/g, '');
    if (valor.length <= 10) {
      valor = valor.replace(/^(\d{2})(\d)/, '($1) $2');
      valor = valor.replace(/(\d{4})(\d)/, '$1-$2');
    } else {
      return aplicarMascaraCelular(valor);
    }
    return valor;
  };

  const aplicarMascaraCEP = (valor) => {
    valor = valor.replace(/\D/g, '');
    if (valor.length <= 8) {
      valor = valor.replace(/^(\d{5})(\d)/, '$1-$2');
    }
    return valor;
  };

  const validarCPF = (cpf) => {
    cpf = cpf.replace(/\D/g, '');
    if (cpf.length !== 11) return false;
    if (/^(\d)\1+$/.test(cpf)) return false;
    
    let soma = 0;
    for (let i = 0; i < 9; i++) {
      soma += parseInt(cpf.charAt(i)) * (10 - i);
    }
    let resto = 11 - (soma % 11);
    let digito1 = resto > 9 ? 0 : resto;
    
    soma = 0;
    for (let i = 0; i < 10; i++) {
      soma += parseInt(cpf.charAt(i)) * (11 - i);
    }
    resto = 11 - (soma % 11);
    let digito2 = resto > 9 ? 0 : resto;
    
    return digito1 === parseInt(cpf.charAt(9)) && digito2 === parseInt(cpf.charAt(10));
  };

  const validarCNPJ = (cnpj) => {
    cnpj = cnpj.replace(/\D/g, '');
    if (cnpj.length !== 14) return false;
    if (/^(\d)\1+$/.test(cnpj)) return false;
    
    let tamanho = cnpj.length - 2;
    let numeros = cnpj.substring(0, tamanho);
    let digitos = cnpj.substring(tamanho);
    let soma = 0;
    let pos = tamanho - 7;
    
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2) pos = 9;
    }
    
    let resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
    if (resultado != digitos.charAt(0)) return false;
    
    tamanho = tamanho + 1;
    numeros = cnpj.substring(0, tamanho);
    soma = 0;
    pos = tamanho - 7;
    
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2) pos = 9;
    }
    
    resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
    return resultado == digitos.charAt(1);
  };

  const validarEmail = (email) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(email);
  };

  const handleChange = (campo) => (e) => {
    let valor = e.target.value;
    
    // Aplicar mscaras especficas
    if (campo === 'cpfResponsavel') {
      valor = aplicarMascaraCPF(valor);
    } else if (campo === 'cnpj') {
      valor = aplicarMascaraCNPJ(valor);
    } else if (campo === 'telefoneResponsavel' || campo === 'telefone') {
      valor = aplicarMascaraTelefone(valor);
    } else if (campo === 'cep') {
      valor = aplicarMascaraCEP(valor);
    }
    
    setFormData({ ...formData, [campo]: valor });
  };

  const handleProximo = () => {
    if (etapa === 0 && !validarDadosInstituicao()) return;
    if (etapa === 1 && !validarDadosResponsavel()) return;
    
    if (etapa < 2) {
      setEtapa(etapa + 1);
    } else {
      handleSubmit();
    }
  };

  const validarDadosInstituicao = () => {
    // Validar campos obrigatrios
    if (!formData.nomeInstituicao.trim()) {
      setErro('Nome da instituio  obrigatrio');
      return false;
    }
    
    if (!formData.cnpj.trim()) {
      setErro('CNPJ  obrigatrio');
      return false;
    }
    
    if (!validarCNPJ(formData.cnpj)) {
      setErro('CNPJ invlido');
      return false;
    }
    
    if (!formData.telefone.trim()) {
      setErro('Telefone  obrigatrio');
      return false;
    }
    
    const telefoneNumeros = formData.telefone.replace(/\D/g, '');
    if (telefoneNumeros.length < 10) {
      setErro('Telefone invlido. Use o formato (99) 9999-9999 ou (99) 9 9999-9999');
      return false;
    }
    
    if (!formData.email.trim()) {
      setErro('Email  obrigatrio');
      return false;
    }
    
    if (!validarEmail(formData.email)) {
      setErro('Email invlido');
      return false;
    }
    
    if (!formData.endereco.trim()) {
      setErro('Endereo  obrigatrio');
      return false;
    }
    
    if (!formData.cidade.trim()) {
      setErro('Cidade  obrigatria');
      return false;
    }
    
    if (!formData.estado.trim()) {
      setErro('Estado  obrigatrio');
      return false;
    }
    
    if (!formData.cep.trim()) {
      setErro('CEP  obrigatrio');
      return false;
    }
    
    const cepNumeros = formData.cep.replace(/\D/g, '');
    if (cepNumeros.length !== 8) {
      setErro('CEP invlido. Use o formato 99999-999');
      return false;
    }
    
    if (!formData.planoId) {
      setErro('Selecione um plano');
      return false;
    }
    
    setErro('');
    return true;
  };

  const validarDadosResponsavel = () => {
    if (!formData.nomeResponsavel.trim()) {
      setErro('Nome do responsvel  obrigatrio');
      return false;
    }
    
    if (!formData.cpfResponsavel.trim()) {
      setErro('CPF do responsvel  obrigatrio');
      return false;
    }
    
    if (!validarCPF(formData.cpfResponsavel)) {
      setErro('CPF invlido');
      return false;
    }
    
    if (!formData.telefoneResponsavel.trim()) {
      setErro('Telefone do responsvel  obrigatrio');
      return false;
    }
    
    const telefoneNumeros = formData.telefoneResponsavel.replace(/\D/g, '');
    if (telefoneNumeros.length < 10) {
      setErro('Telefone invlido');
      return false;
    }
    
    if (!formData.emailResponsavel.trim()) {
      setErro('Email do responsvel  obrigatrio');
      return false;
    }
    
    if (!validarEmail(formData.emailResponsavel)) {
      setErro('Email do responsvel invlido');
      return false;
    }
    
    if (!formData.cargoResponsavel.trim()) {
      setErro('Cargo do responsvel  obrigatrio');
      return false;
    }
    
    setErro('');
    return true;
  };

  const handleSubmit = () => {
    if (!formData.loginAdmin.trim()) {
      setErro('Login  obrigatrio');
      return;
    }
    
    if (formData.loginAdmin.length < 4) {
      setErro('Login deve ter no mnimo 4 caracteres');
      return;
    }
    
    if (!formData.senhaAdmin.trim()) {
      setErro('Senha  obrigatria');
      return;
    }

    if (formData.senhaAdmin.length < 6) {
      setErro('A senha deve ter no mnimo 6 caracteres');
      return;
    }
    
    if (!formData.confirmarSenha.trim()) {
      setErro('Confirme a senha');
      return;
    }

    if (formData.senhaAdmin !== formData.confirmarSenha) {
      setErro('As senhas no conferem');
      return;
    }

    // Abrir modal de contrato
    setErro('');
    setMostrarContrato(true);
  };

  const handleContratoAceito = (dadosAceite) => {
    setContratoAceito(dadosAceite);
    setMostrarContrato(false);

    try {
      const planoSelecionado = planosDisponiveis.find(p => p.id === parseInt(formData.planoId));
      
      // Redirecionar para a pgina de pagamento com dados do contrato
      navigate('/pagamento', {
        state: {
          dadosCadastro: formData,
          planoSelecionado: planoSelecionado,
          contratoAceito: dadosAceite
        }
      });
    } catch (error) {
      setErro('Erro ao processar. Tente novamente.');
    }
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ minHeight: '100vh', py: 4 }}>
        <Paper elevation={3} sx={{ p: 4 }}>
          <Box sx={{ textAlign: 'center', mb: 3 }}>
            <SchoolIcon sx={{ fontSize: 60, color: 'primary.main', mb: 2 }} />
            <Typography variant="h4" gutterBottom>
              Cadastro de Nova Instituio
            </Typography>
            <Typography variant="subtitle1" color="text.secondary">
              Sistema CEI - Controle Escolar Inteligente
            </Typography>
          </Box>

          <Stepper activeStep={etapa} sx={{ mb: 4 }}>
            {etapas.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>

          {erro && (
            <Alert severity="error" sx={{ mb: 2 }} onClose={() => setErro('')}>
              {erro}
            </Alert>
          )}

          {/* ETAPA 0 - Dados da Instituio */}
          {etapa === 0 && (
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  label="Nome da Instituio *"
                  fullWidth
                  required
                  value={formData.nomeInstituicao}
                  onChange={handleChange('nomeInstituicao')}
                  placeholder="Ex: Escola Municipal Joo Silva"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  label="CNPJ *"
                  fullWidth
                  required
                  value={formData.cnpj}
                  onChange={handleChange('cnpj')}
                  placeholder="99.999.999/9999-99"
                  helperText="Formato: 99.999.999/9999-99"
                  inputProps={{ maxLength: 18 }}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  label="Cdigo INEP"
                  fullWidth
                  value={formData.inep}
                  onChange={handleChange('inep')}
                  placeholder="12345678"
                  helperText="Cdigo de 8 dgitos (opcional)"
                  inputProps={{ maxLength: 8 }}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  label="Telefone/Celular *"
                  fullWidth
                  required
                  value={formData.telefone}
                  onChange={handleChange('telefone')}
                  placeholder="(99) 9 9999-9999"
                  helperText="Formato: (99) 9 9999-9999"
                  inputProps={{ maxLength: 16 }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Email Institucional *"
                  type="email"
                  fullWidth
                  required
                  value={formData.email}
                  onChange={handleChange('email')}
                  placeholder="contato@escola.com.br"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Endereo *"
                  fullWidth
                  required
                  value={formData.endereco}
                  onChange={handleChange('endereco')}
                  placeholder="Rua, nmero, complemento"
                />
              </Grid>
              <Grid item xs={12} sm={5}>
                <TextField
                  label="Cidade *"
                  fullWidth
                  required
                  value={formData.cidade}
                  onChange={handleChange('cidade')}
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <TextField
                  label="Estado *"
                  fullWidth
                  required
                  value={formData.estado}
                  onChange={handleChange('estado')}
                  placeholder="Ex: SP, RJ, MG"
                  inputProps={{ maxLength: 2, style: { textTransform: 'uppercase' } }}
                />
              </Grid>
              <Grid item xs={12} sm={3}>
                <TextField
                  label="CEP *"
                  fullWidth
                  required
                  value={formData.cep}
                  onChange={handleChange('cep')}
                  placeholder="99999-999"
                  helperText="Formato: 99999-999"
                  inputProps={{ maxLength: 9 }}
                />
              </Grid>
              
              <Grid item xs={12}>
                <TextField
                  select
                  label="Escolha seu Plano *"
                  fullWidth
                  required
                  value={formData.planoId}
                  onChange={handleChange('planoId')}
                  helperText="Selecione a durao da sua assinatura"
                >
                  {planosDisponiveis.length === 0 ? (
                    <MenuItem value="" disabled>
                      Nenhum plano disponvel no momento
                    </MenuItem>
                  ) : (
                    planosDisponiveis.map((plano) => (
                      <MenuItem key={plano.id} value={plano.id}>
                        {plano.nome} - R$ {plano.valor.toFixed(2)} ({plano.dias} dias)
                      </MenuItem>
                    ))
                  )}
                </TextField>
                {formData.planoId && planosDisponiveis.find(p => p.id === parseInt(formData.planoId)) && (
                  <FormHelperText>
                    Valor por dia: R$ {(planosDisponiveis.find(p => p.id === parseInt(formData.planoId)).valor / 
                      planosDisponiveis.find(p => p.id === parseInt(formData.planoId)).dias).toFixed(2)}
                  </FormHelperText>
                )}
              </Grid>
            </Grid>
          )}

          {/* ETAPA 1 - Dados do Responsvel */}
          {etapa === 1 && (
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  label="Nome Completo do Responsvel *"
                  fullWidth
                  required
                  value={formData.nomeResponsavel}
                  onChange={handleChange('nomeResponsavel')}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  label="CPF *"
                  fullWidth
                  required
                  value={formData.cpfResponsavel}
                  onChange={handleChange('cpfResponsavel')}
                  placeholder="999.999.999-99"
                  helperText="Formato: 999.999.999-99"
                  inputProps={{ maxLength: 14 }}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  label="Telefone/Celular *"
                  fullWidth
                  required
                  value={formData.telefoneResponsavel}
                  onChange={handleChange('telefoneResponsavel')}
                  placeholder="(99) 9 9999-9999"
                  helperText="Formato: (99) 9 9999-9999"
                  inputProps={{ maxLength: 16 }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Email *"
                  type="email"
                  fullWidth
                  required
                  value={formData.emailResponsavel}
                  onChange={handleChange('emailResponsavel')}
                  placeholder="responsavel@email.com"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Cargo/Funo *"
                  fullWidth
                  required
                  value={formData.cargoResponsavel}
                  onChange={handleChange('cargoResponsavel')}
                  placeholder="Ex: Diretor, Coordenador Pedaggico"
                />
              </Grid>
            </Grid>
          )}

          {/* ETAPA 2 - Criar Acesso */}
          {etapa === 2 && (
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Alert severity="info" sx={{ mb: 2 }}>
                  Crie seu login e senha para acessar o sistema aps a aprovao
                </Alert>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Login de Acesso *"
                  fullWidth
                  required
                  value={formData.loginAdmin}
                  onChange={handleChange('loginAdmin')}
                  helperText="Mnimo 4 caracteres. Sem espaos."
                  inputProps={{ minLength: 4 }}
                  autoComplete="username"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Senha *"
                  type="password"
                  fullWidth
                  required
                  value={formData.senhaAdmin}
                  onChange={handleChange('senhaAdmin')}
                  helperText="Mnimo 6 caracteres"
                  inputProps={{ minLength: 6 }}
                  autoComplete="new-password"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="Confirmar Senha *"
                  type="password"
                  fullWidth
                  required
                  value={formData.confirmarSenha}
                  onChange={handleChange('confirmarSenha')}
                  helperText="Digite a mesma senha novamente"
                  autoComplete="new-password"
                />
              </Grid>
            </Grid>
          )}

          <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 4 }}>
            <Button
              disabled={etapa === 0}
              onClick={() => setEtapa(etapa - 1)}
            >
              Voltar
            </Button>
            <Box>
              <Button onClick={() => navigate('/login')} sx={{ mr: 1 }}>
                Cancelar
              </Button>
              <Button
                variant="contained"
                onClick={handleProximo}
              >
                {etapa === 2 ? 'Finalizar Cadastro' : 'Prximo'}
              </Button>
            </Box>
          </Box>
          
          {/* Marca Registrada no rodap do formulrio */}
          <Box sx={{ mt: 4, pt: 3, borderTop: 1, borderColor: 'divider', textAlign: 'center' }}>
            <Typography variant="caption" color="text.secondary" display="block">
               {new Date().getFullYear()} CEI - Controle Escolar Inteligente
            </Typography>
            <Typography variant="caption" color="text.secondary" display="block">
              Sistema desenvolvido por <strong>Wander Pires Silva Coelho</strong> 
            </Typography>
          </Box>
        </Paper>

        {/* Modal de Contrato */}
        <ContratoModal
          open={mostrarContrato}
          onClose={() => setMostrarContrato(false)}
          onAceitar={handleContratoAceito}
          dadosInstituicao={{
            nome: formData.nomeInstituicao,
            cnpj: formData.cnpj,
            responsavel: formData.nomeResponsavel,
            plano: planosDisponiveis.find(p => p.id === parseInt(formData.planoId))?.nome || 'No selecionado'
          }}
        />
      </Box>
    </Container>
  );
}
